.project-details{
    display: grid;
    grid-template-columns: 3fr 2fr;
    align-items: start;
    grid-gap: 60px;
}

.project-summary {
    background-color: #fff;
    padding: 30px;
    border-radius: 4px;
}

.project-summary .due-date {
    margin: 10px 0;
    font-size: 0.9em;
    color: var(--title-color)
}
.project-summary .details {
    margin: 10px 0;
    color: var(--text-color);
    line-height: 1.8em;
    font-size: 0.9em;

}
.project-summary h4{
    color: var(--text-color);
    font-size: 0.9em;
}
.project-summary .assigned-users{
    display: flex;
    margin-top: 20px;
}
.project-summary .assigned-users .avatar {
    margin-right: 10px;
}
.project-summary .assigned-users .user-name{
    color: var(--text-color);
}
.project-summary + .btn {
    margin-top: 20px;
}

/* project comment */
.project-comments label {
    margin-bottom: 0px;
}
.project-comments textarea{
    min-height: 40px;
    font-size: 1.5em;
}

/* project comment list*/
.project-comments h4{
    color: car(--heading-color);
}
.project-comments li{
    padding: 16px;
    border-radius: 4px;
    border: 1px solid #f2f2f2;
    margin-top: 20px;
    box-shadow: 3px 3px 5px rgb(0, 0, 0, 0.5);
    background-color: #fff;
}
.comment-author {
    display: flex;
    align-items: center;
    color: var(--title-color);
}
.comment-author .avatar {
    width: 30px;
    height: 30px;
    margin-right: 10px;
}
.comment-date{
    color: var(--text-color);
    font-size: 0.9em;
}
.comment-content{
    color: var(--text-color);
    font-size: 0.9em;
}